  {
    label: () => user.value?.username,
    key: 'user',
    children: [
      {
        label: '我的资料',
        key: 'aboutMe'
      },
      {
        label: '修改信息',
        key: 'editFile'
      },
      {
        label: '登出',
        key: 'logout'
      }
    ]
  }
]

function onUserMenuUpdate (key: string) {
  switch (key) {
  case 'aboutMe':
    inputShouldShowUserMenuModal.value = true
    break
  case 'editFile':
    inputShouldShowUserEditModal.value = true
    break
  /* case 'removeAccount':
